@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSans-kr.css);
html, body {
    font-size: 14px;
    position: relative;
    width: 100%;
    height: 100%;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Spoqa Han Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #eff2f5;
    color: #050505;
}

input, button, textarea {
    outline: none;
    font-family: 'Noto Sans KR', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 14px;
}

#root {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}

a {
    text-decoration: none;
}

.md-width, .sm-width {
    margin: 0 auto;
}

.md-width {
    max-width: 940px;
}

.sm-width {
    max-width: 908px;
}
.row {
    color: #050505;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.row.last-row {
    margin-bottom: 0;
}

.row:after {
    clear: both;
}

.row:after {
    content: " ";
    display: table;
}

.row h2 {
    position: relative;
    display: flex;
    flex-basis: 100%;
    justify-content: flex-start;
    align-items: center;
    margin: 0 0 1.14rem;
    font-size: 1.14rem;
    text-align: left;
    font-weight: 600;
}

.row h2.font-md {
    font-size: 1.28rem;
}

.row h2.font-lg {
    font-size: 1.42rem;
}

.row > .col-1, .row > .col-2, .row > .col-3, .row > .col-4, .row > .col-5, .row > .col-6, .row > .col-7, .row > .col-8, .row > .col-9, .row > .col-10, .row > .col-11, .row > .col-12 {
    margin: .57rem;
}

.row > .col-12 {
    padding: 0 !important;
}

.row .col-1 {
    width: calc(100% / 12);
}

.row .col-2 {
    width: calc(100% / 6);
}

.row .col-3 {
    width: calc(100% / 4);
}

.row .col-4 {
    width: calc(100% / 3);
}

.row .col-5 {
    width: calc(100% / 2.4);
}

.row .col-6 {
    width: calc(100% / 2);
}

.row .col-7 {
    width: calc(calc(100% / 12) * 7);
}

.row .col-8 {
    width: calc(100% / 1.5);
}

.row .col-9 {
    width: calc(calc(100% / 12) * 9);
}

.row .col-10 {
    width: calc(100% / 1.2);
}

.row .col-11 {
    width: calc(calc(100% / 12) * 11);
}

.row .col-12 {
    width: 100%;
}

.row .col {
    border-radius: .8rem;
    margin-bottom: 1.14rem;
    padding: 1.14rem;
    background-color: #FFF;
    width: 100%;
    box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
}
